const Background = styled.div`
    width: 100%;
    padding: 100px 0;
`;
const Container = styled.article`
    width: 1180px;
    margin: 0 auto;
    padding: 20px;
    @media ${props => props.theme.desktop}{
        width: 100%;
    }
`;
const Title = styled.h2`
    text-align: center;
    margin-bottom: 30px;
    font-size: 4em;
    font-weight: 800;
    @media ${props => props.theme.tablet}{font-size: 3em;}
    @media ${props => props.theme.mobile}{font-size: 2em;}
`;
const Subtitle = styled.p`
    text-align: center;
    color: gray;
`
const Section = styled.section`
    margin-top: 70px;
    display: grid;
    grid-template-rows: 3fr 4fr;
    grid-template-columns: 1fr 1fr;
    grid-gap: 40px;
    &>div:first-child{
        grid-column: 1 / 2;
	    grid-row: 1 / 3;
    }
    @media ${props => props.theme.tabletL}{
        display: flex;
        flex-direction: column;
    }
    @media ${props => props.theme.mobile}{
        margin-top: 20px;
    }
`
const Tab = styled.div`
    padding: 30px ;
    border-radius: 10px;
    box-shadow: 3px 2px 5px 1px rgba(0, 0, 0, 0.3);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    @media ${props => props.theme.mobile}{
        padding: 15px;
    }
`;
const TabTitle = styled.h4`
    font-size: 2.2em;
    font-weight: 700;
    margin-bottom: 30px;
    @media ${props => props.theme.mobile}{
        letter-spacing: -0.05em;
        font-size: 2em;
    }
`;
const TabIcon = styled.i`
    margin-right: 10px;
`;
const TabContent = styled.div`
    line-height: 1.5em;
    display: flex;
    justify-content: space-around;
    width: 100%;
    flex-wrap: wrap;
    
`;
const WhoAmI = styled.div`
    width: 250px; height: 250px;
    margin: 0 auto;
    border-radius: 50%;
    background-color: lightgray;
    @media ${props => props.theme.mobile}{
        width: 60vw; height: 60vw;
    }
`;
const WhoAmIName = styled.h3`
    font-size: 1.5em;
    font-weight: 600;
    margin: 10px;
`;
const WhoAmITxt = styled.div`
    width: 80%;
    margin: 5px 0 20px 0;
    font-size: 0.9em;
    line-height: 1.4em;
`;
const Icon = styled.i`
    font-size: 5.5em;
`;
const List = styled.h4`
    font-size: 1.1em;
    margin-bottom: 10px;
    span{font-weight: bold;}
    @media ${props => props.theme.mobile}{
        text-align: center;
    }
`;
const Listdetail = styled.p`
    margin-top: 10px;
    color: gray;
    font-size: 0.9em;
    line-height: 1.2em;
    @media ${props => props.theme.mobile}{
        margin-bottom: 20px;
    }
`
const Image = styled.div`
    width: 25%; height: 130px;
    background-image: ${props => `url(${props.img})`};
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    @media ${props => props.theme.mobile}{
        width: 50%; height: 110px;
        margin-bottom: 20px;
    }
`;

const AboutMeIcon = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 30%;
    &>span:nth-child(2){
        font-size: 1.5em;
        font-weight: 600;
        margin: 10px;
    }
    &>span:nth-child(3){
        font-size: 0.8em;
    }

    @media ${props => props.theme.mobile}{
        width: 100%;
        margin-top: 25px;
        &>span:nth-child(2){
            font-size: 1.3em;
            margin: 0px;
        }
    }
`